@import url(https://fonts.googleapis.com/css2?family=Syne+Mono&display=swap);
body {
  font-family: 'Syne Mono', monospace;
  background: linear-gradient(-45deg, #1e9b41, #a5d22d, #2d4196, #c30a28);
  background-size: 1000% 1000%;
  animation: rainbow 75s ease infinite;
  text-align: center;
  color: antiquewhite;
}

button {
  font-family: inherit;
  padding: 1rem;
  margin: 1rem;
  background: white;
  box-shadow: 0 0 5px #747474;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  text-transform: uppercase;
}

@keyframes rainbow {
  0% {
    background-position: 0% 82%;
  }
  50% {
    background-position: 100% 19%;
  }
  100% {
    background-position: 0% 82%;
  }
}

.circle {
  height: 150px;
  width: 150px;
  border-radius: 50%;
  display: inline-block;
  margin: 1rem;
  cursor: pointer;
}

.beer1 {
  background-image: url(/speedgame/static/media/beer1.d1df32e7.svg);
  opacity: 0.3;
}

.beer2 {
  background-image: url(/speedgame/static/media/beer2.66d9582a.svg);
  opacity: 0.3;
}

.beer3 {
  background-image: url(/speedgame/static/media/beer3.a0b6f0de.svg);
  opacity: 0.3;
}

.beer4 {
  background-image: url(/speedgame/static/media/beer4.1b5cc4cb.svg);
  opacity: 0.3;
}

.circle.active {
  opacity: 1;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: grid;
  justify-items: center;
  align-items: center;
}

.gameover_box {
  background: white;
  color: black;
  padding: 1rem;
  width: 40%;
  min-height: 25%;
  border-radius: 5px;
  position: relative;
  display: grid;
  justify-items: center;
  align-items: center;
}

.gameover_box button {
  background: lightgray;
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: pointer;
}

